/* eslint-disable react-hooks/exhaustive-deps */
import classNames from 'classnames/bind';
import { memo, useEffect, useRef } from 'react';
import styles from './airquality.module.scss';

const cx = classNames.bind(styles);

function CheckAqi({ aqi = 5, parent }) {
    const sendData = () => {
        if (notePeople.current === '') {
            notePeople.current =
                'Hy nhn su vo thin nhin, bn s thu hiu mi th r rng hn.';
        }
        parent([notePeople.current]);
    };

    let state = useRef('');
    let des = useRef('');
    let notePeople = useRef('');
    let classCus = useRef('');

    useEffect(() => {
        sendData();
        if (aqi === 5) {
            classCus.current = 'red';
            state.current = 'Very Poor';
            des.current =
                'C hi cho sc khe cho hu ht mi ngi. Mi ngi u c th tri nghim nh hng sc khe. Nhm cm ng c th b nh hng nng n hn.';
            notePeople.current =
                'Phn cn li ca th gii (thin nhin) c th tip tc sng m khng c chng ta, nhng chng ta khng th tn ti nu thiu i chng.';
        }
        if (aqi === 4) {
            classCus.current = 'red';
            state.current = 'Poor';
            des.current =
                'Mi ngi c th bt u b nh hng sc khe; thnh vin ca cc nhm nhy cm c th b nh hng sc khe nghim trng hn.';
            notePeople.current =
                'Chng ta nn bit tn trng mi sinh vt sng v thay th bo lc bng s bao dung, thu hiu v mt tnh yu to ln.';
        }
        if (aqi === 3) {
            classCus.current = '';
            state.current = 'Normal';
            des.current =
                'Cht lng khng kh  mc chp nhn c. Tuy nhin, mt s cht gy  nhim c th nh hng n sc khe ca nhng ngi nhy cm vi khng kh b  nhim.';
            notePeople.current =
                'Hy nhn su vo thin nhin, bn s thu hiu mi th r rng hn.';
        }
        if (aqi === 2) {
            classCus.current = 'green';
            state.current = 'Good';
            des.current =
                'Cht lng khng kh t tiu chun v  nhim khng kh c coi l t hoc khng nguy him.';
            notePeople.current =
                'C mt s tht khng th chi b rng mt nhm nh cng dn c  thc v tn tm c th thay i c th gii.';
        }
        if (aqi === 1) {
            classCus.current = 'green';
            state.current = 'Good';
            des.current =
                'Cht lng khng kh t tiu chun v  nhim khng kh c coi l t hoc khng nguy him.';
            notePeople.current =
                'Hy nh rng Tri t thch cm nhn i chn trn v ln gi lun thch chi a vi mi tc ca bn.';
        }
    }, [aqi]);

    return (
        <div className={cx('polluted-state')}>
            <div className={cx('aqi', classCus.current)}>
                <p>{state.current}</p>
            </div>
            <div className={cx('des-aqi')}>{des.current}</div>
        </div>
    );
}

export default memo(CheckAqi);
